.wrapper{position:relative;display:flex;flex-direction:column;min-width:100%}.label{font-family:"Raleway",Arial,sans-serif;font-size:1.3rem;font-weight:500;line-height:1.6rem;color:#797979;opacity:.7}.input{margin:0;padding:0;border:none;box-shadow:none;font-family:"Open Sans",Arial,sans-serif;font-size:1.4rem;font-weight:600;line-height:1.9rem;color:#363636;margin-top:.7rem;padding:1.4rem 2rem;height:5rem;border:1px solid #c3c3c3;border-radius:.2rem;transition-duration:.3s}.input:hover,.input:focus{border-color:#f9cf1c}.inputError{color:#ec8c1d;border-color:#ec8c1d}.inputDisabled{background-color:#dadada;border-color:#dadada;pointer-events:none}.wrapperCash{position:relative}.wrapperCash::before{font-family:"Open Sans",Arial,sans-serif;font-size:1.4rem;font-weight:600;line-height:1.9rem;color:#363636;content:"€";position:absolute;bottom:1.5rem;left:2rem}.wrapperCash .input{padding-left:3.5rem}